.wall {
  display: grid;
  grid-template-columns: repeat(4, minmax(6em,max-content));
}

.wall > * {
  width: auto;
  border: 1px black solid;
}

.selected {
  color: red;
}

.group1 {
  background-color: cornsilk;
}

.group2 {
  background-color: red;
}

.group3 {
  background-color: green;
}

.group4 {
  background-color: cyan;
}